.nav {
    grid-area: nav;
    display: flex;
    flex-direction: column;
    border-radius: 15px;
}

.navlink {
    padding: 10px;
    background-color: rgba(21, 34, 42, 0.9);
    border: 1px solid rgba(112, 128, 144, 0.9);
    border-radius: 8px;
    color: white;
    text-decoration: none;
    font-size: 20px;
    margin: 0 0 15px 0;
}

.navlink:hover {
    color: aquamarine;
    border: 1px solid aquamarine;
}

.navlinkActive {
    color: black;
    background-color: rgba(255, 255, 255, 1);
    border: 1px solid white;
}

.navlinkActive:hover {
    color: black;
    background-color: rgba(255, 255, 255, 1);
    border: 1px solid white;
}
